@mixin bg($url, $pos: center, $size: 100% auto){
    background: url('../img/#{$url}') no-repeat $pos / $size;
}

@mixin mask($url, $bgColor: $c-gray-1){
    --bg-color: #{$bgColor};
    mask-image: url('../img/#{$url}');
    -webkit-mask-clip: content;
    -webkit-mask-size: contain;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: center;
    background-color: var(--bg-color);
}

@mixin font($name, $style, $size, $weight, $color){
    font-family: $name;
    font-size: $size+'px';
    font-weight: $weight;
    color: $color;
}

@mixin blind(){
    overflow: hidden;
    border: 0;
    position: absolute;
    z-index: -1;
    width: 1px;
    height: 1px;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

@mixin flex($justify: null, $align: center){
    display: flex;
    align-items: unquote($align);
    @if($justify){
        justify-content: unquote($justify);
    }
}